         scroll_target: ".sectionDevice"
       },{
         title: "",
         content: "",
         btn_label: "",
         image: "/static/img/service_1.svg",
         scroll_target: ".sectionWorkshop"
       }
     ],
    
    workshops: [
      {
        length: "?",
        cover: "/static/img/workshop_1.svg",
        title: "",
        content: "",
        detail: {
          length: "",
          appointment: `1. 3hr<br>
          2. `,
          requirement: "",
          other: "<br><br><br>",
          target: "",
          content: `
          1. <br>
          2. <br>
          3. <br>
          4. <br>`
        }
      },{
        length: "3",
        cover: "/static/img/workshop_2.svg",
        title: "",
        content: "(mindset)",
        detail: {
          length: "",
          appointment: ``,
          requirement: `<br>
              <br>
